import React from 'react'
import '../style/blogcard.scss'
import { Link } from "gatsby";

class BlogCard extends React.Component{

    constructor(props) {
        super(props);
    }
  
    render () {
    return (
        <Link class="blog-item" to={this.props.url}>
            <header class="card__thumb">
                <img src={this.props.photo} class="cover"/>
              
            </header>
            <div class="card__body">
              <div class="card__category">{this.props.category}</div>
              <div class="card__title">{this.props.title}</div>
              <div class="card__subtitle">{this.props.subTitle}</div>
              <p class="card__description">{this.props.excerpt}</p>
            </div>
        </Link>
    );
   }
};
export default BlogCard;