import React from 'react';
import {Component} from 'react';
import { Link } from 'gatsby';
import '../components/header';
import '../style/blog.scss';
import Header from '../components/header';
import Footer from '../components/footer';
import background from '../images/showcase.jpg'
import stairs from '../images/stairs.jpg'
import Showcase from '../components/showcase';
import BlogCard from '../components/BlogCard';
import { Pagination, PaginationItem, PaginationLink } from 'reactstrap';

class Blog extends Component {

  constructor(props) {
    super(props);
    this.state = {
        postPerPage: 6,
        pages: 0
    };
  } 

  componentWillMount(){
    const pages = Math.ceil(this.props.data.allContentfulBlog.edges.length / this.state.postPerPage);
    this.setState({pages: pages})
  }
  

  render() {
    
    return(
  <>
    <Header color="black"/>
    <Showcase background={background} name='BE RELENTLESS'/>
    <section id="blog-grid" className="mx-5 py-5 my-5">
      {this.props.data.allContentfulBlog.edges.map((edge) => {
        return <BlogCard photo={edge.node.photo.file.url} url={edge.node.slug} category={edge.node.category} excerpt={edge.node.excerpt} title={edge.node.title} subTitle={edge.node.subTitle} />
      })}
    </section>
    
    <Footer />
  </>
);
}
}

export default Blog;

export const pageQuery = graphql`
  query pageQuery{
   allContentfulBlog {
		edges {
		  node {
		    id
        contentful_id
        slug
        category
        title
        subTitle
        excerpt
        photo{
          file{
            url
          }
        }
		  }
		}
  }
}
`